    -webkit-filter: brightness(70%);
    filter: brightness(70%);
    -webkit-transition: all ease 0.25s;
    transition: all ease 0.25s;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.grid figure figcaption {
    padding-right: 5px;
    padding-left: 5px;
    color: #fff;
    font-size: 1.25em;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

